






import { Component, Vue, Prop } from 'vue-property-decorator';
import { getNavigations } from '@/services/navigation';

@Component({})
export default class Navigation extends Vue {
  private navdata: any[];

  constructor() {
    super();
    this.navdata = [];
  }
  private mounted(): void {
    getNavigations().then((res: any[]) => {
      this.navdata = res;
    });
  }
}
