









import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({})
export default class StyledList extends Vue {
    @Prop({default: ''})
    private data?: any;

    get title(): string {
        const { title } = this.data;
        return title;
    }
    get content(): any[] {
        const { list } = this.data;
        return list;
    }
}
